.c-reasons {
    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include mq($from: desktop) {
            flex-direction: row;
            justify-content: space-between;
            align-items: flex-start;
        }
    }

    &__item {
        max-width: 300px;
        margin: 0 0 56px;

        &:last-child {
            margin-bottom: 0;
        }

        @include mq($from: desktop) {
            max-width: 260px;
            margin-bottom: 0;

            &:nth-child(2) {
                margin-top: 32px;
            }
        }
    }

    &__image {
        width: 52px;
        height: auto;
        display: block;
        margin: 0 auto;
    }

    &__content {
        margin-top: 12px;
        text-align: center;
    }

    &__title {
        font-weight: 600;
        font-size: 18px;
        margin: 0 0 5px 0;
    }

    &__text {
        line-height: 1.5;
        font-size: 16px;
    }
}