/*/ ========================================================================
//   #KC TEASER
//   ======================================================================== */
.c-kc-teaser {
    display: flex;
    flex-wrap: wrap;

    &__item {
        width: 100%;
        background-color: $color-white;
        box-shadow: 0px 4px 12px rgba(65, 70, 72, 0.12);
        margin: 0 15px 24.5px;

        @include mq($from: desktop) {
            margin: 0 25px 25px;
        }

        &--50 {
            @include mq($from: tablet) {
                width: calc(50% - 51px);
            }
        }

        &--33 {
            @include mq($from: desktop) {
                width: calc(33.33% - 51px);
            }
        }
    }

    &-preview {

        &__content {
            padding: 21px 17px 19px;
        }

        &__img {
            min-height: 186px;
            height: 100%;
        }

        &__img--link {
            display: block;
            width: 50%;
        }
        a {
            text-decoration: none;
            display: block;
        }

        &__title {
            font-size: 24px;
            font-weight: 600;
            margin: 8px 0 6px;
            line-height: 1.3;

            a {
                color: $color-black;
                &:hover {
                    color: $color-blue;
                    text-decoration: underline;
                }
            }
        }

        &__link {
            font-size: 18px;
            font-weight: 600;
            color: $color-black;
            &:hover {
                color: $color-blue;
                text-decoration: underline;
            }
        }

        &__text {
            margin-top: 0;
            margin-bottom: 26px;
        }
    }
}