///* ========================================================================
//   #MENU MOBILE
//   ======================================================================== */

.c-menu-mobile {
    background-color: #fff;
    height: 100vh;
    &__header {
        padding: 22px 20px;
        display: flex;
        justify-content: flex-end;
        background-color: $color-blue;

        .c-site-header-controls__control {
            color: #fff;
        }
    }

    &__content {        
        padding: 26px 20px;
        overflow: auto;
        height: calc(100vh - 80px);
    }

    &__controls {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__search {
        margin-top: 24px;
    }

    &__nav {
        margin-top: 20px;
    }

    &__social-media {
        margin-top: 32px;
    }
}