.c-product-hero {
    display: flex;
    flex-direction: column-reverse;
    background-color: $color-light-blue;
    @include mq($from: desktop) {
        position: relative;
    }

    &__img {
        height: 156px;
        @include mq($from: tablet) {
            height: 240px;
        }
        @include mq($from: desktop) {
            height: 400px;
        }
    }

    &__content {
        padding: 30px 0 55px;
        @include mq($from: desktop) {
            position: absolute;
            top: 50%;
            left: 0;
            width: 100%;
            z-index: 5;
            padding: 20px 0;
            transform: translateY(-50%);
        }
    }

    &__title {
        text-align: center;
        font-size: 36px;
        font-family: $font-oswald-medium;
        font-weight: normal;
        color: $color-blue;
        line-height: 1.08;
        margin: 0;
        @include mq($from: desktop) {
            text-align: left;
            font-size: 44px;
            max-width: 650px;
        }
        @include mq($from: wide) {
            font-size: 52px;
        }
    }

    &__text {
        margin-top: 8px;
        font-family: $font-oswald;
        font-size: 26px;
        text-align: center;
        line-height: 1;
        @include mq($from: desktop) {
            text-align: left;
            font-size: 31px;
        }
        @include mq($from: wide) {
            font-size: 36px;
        }

        p {
            margin: 0 0 10px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    &__controls {
        padding: 23px 0;
        @include mq($until: desktop) {
            text-align: center;
        }
        .c-button {
            padding: 5px 54px;
            @include mq($from: desktop) {
                padding: 5px 37px;
            }

        }
        &-text-mobile {
            @include mq($from: desktop) {
                display: none;
            }
        }
        &-text-desktop {
            display: none;
            @include mq($from: desktop) {
                display: block;
            }
        }
    }

    &__quote {
        background-color: #fff;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
        border-radius: 8px;
        padding: 22px 22px 32px;
        max-width: 715px;
        margin: 24px auto 0;
        @include mq($from: tablet) {
            padding: 14px 24px;
        }
        @include mq($from: desktop) {
            max-width: 750px;
            margin: 32px 0 0 0;

            html[lang=es-US] & {
                max-width: 780px;
            }
        }
    }
}