.c-faqs-page-accordion {
    list-style: none;
	
	&__item {
		position: relative;
        margin-bottom: 15px;
        padding-bottom: 15px;
        border-bottom: 1px solid #8AB2CB;
        @include mq($from: desktop){
            margin-bottom: 40px;
            padding-bottom: 24px;
        }
    }
	
    &__title {
        color: $color-black;
        font-weight: 600;
        font-size: 18px;
        //border-bottom: 1px solid #8AB2CB;
        line-height: 1.7;
        padding-bottom: 3px;
        display: flex;
        padding-right: 15px;
        position: relative;

        .o-icon {
            font-size: 16px;
            margin-right: 8px;
            color: $color-blue;
            transform: translate(0 , 10px);
        }
        @include mq($from: desktop){
            font-size: 22px;
        }

        &:hover {
            color: $color-blue;
        }

		// &:after {
		// 	border-right: 2px solid $color-blue;
		// 	border-bottom: 2px solid $color-blue;
		// 	position: absolute;
		// 	right: 0;
		// 	content: " ";
		// 	top: 0;
        //     bottom: 0;
        //     margin: auto;
		// 	transform: rotate(-45deg);
		// 	transition: 0.4s ease-in-out;
        //     width: 8px;
        //     height: 8px;
        // }	
        &.active {
            //color: $color-blue;
            // &:after {
            //     transform: rotate(45deg);
            // }
        }
    }

    &__content{
        //display: none;
        padding: 0 25px 0;
        
        a {
            text-decoration: none;
        }
        p {

            a,strong {
                color: $color-blue;
            }
        }

        ul {
            list-style: none;
            li{
                margin-bottom: 16px;
                &:last-child{
                    margin-bottom: 0;
                }
                span {
                    display: block;
                    font-weight: 600;
                }
                strong {
                    color: $color-blue;
                }
            }
        }
       
        
    }

    h3 {
        color: $color-blue;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 18px;
        padding: 20px 0;
        @include mq($from: desktop){
            font-size: 22px;
        }
    }
}
	