.c-regional-page-product {
    .c-regional-page-hero__title {
        text-align: center;
    }
    .c-regional-page-quote__wrapper {
        text-align: center;
        margin: auto;
    }
    .c-regional-page-quote__form-input {
        box-shadow: 0px 0px 6px rgba(0, 71, 187, 0.8);
    }
    &__title {
        text-align: center;
        font-weight: 600;
        font-size: 24px;
        max-width: 940px;
        margin: auto;
        line-height: 1.25;
        margin-bottom: 30px;
        @include mq($from: desktop) {
            strong {
                font-weight: 600;
            }
        }
    }
}
/*/ ========================================================================
//   #Card State Regional Page
//   ======================================================================== */
.c-regional-page-states {
    >.o-wrapper.o-wrapper--large {
        @include mq($from: tablet) {
            padding: 0;
        }
    }
}

.c-regional-page-state {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 40px;
   

    &__item {
        width: 100%;
        max-width: 360px;
        background-color: $color-white;
        box-shadow: 0px 4px 12px rgba(65, 70, 72, 0.12);
        margin: 0 5px 24px;
        position: relative;
        border-radius: 8px;

        @include mq($from: tablet) {
            margin: 0 13px 56px;
            max-width: 310px;
        }
        @include mq($from: desktop) {
            max-width: 360px;
            margin: 0 20px 56px;
        }
        
    }

    &-preview {

        &__content {
            padding: 21px 17px 19px;
            @include mq($until: tablet) {
                padding: 21px 17px 5px;
            }
        }

        &__img {
            min-height: 124.7px;
            height: 100%;
            @include mq($from: tablet) {
                min-height: 232px;
            }
        }

        &__img-link {
            display: block;
            overflow: hidden;
        }
        &__title {
            font-size: 28px;
            font-weight: 500;
            margin: 0 0 20px;
            line-height: 1.3;
            color: $color-blue;
            font-family: $font-oswald;
            text-align: center;
            @include mq($from: tablet) {
                text-align: left;
            }

            a {
                color: inherit;
                @include mq($until: tablet) {
                    text-decoration: none;
                }
                &:hover {
                    text-decoration: underline;
                }
            }
        }

        &__link {
            font-size: 18px;
            font-weight: 600;
            width: 100%;
            height: 45px;
            color: $color-black;
            @include mq($until: tablet) {
                display: none;
            }
            .c-button {
                position: absolute;
                bottom: 20px;
            }
        }

        &__text {
            margin-top: 0;
            margin-bottom: 26px;
            @include mq($until: tablet) {
                display: none;
            }
            
            a {
                color: $color-blue;
                font-weight: 600;
                text-decoration: underline;
            }
        }
    }
}