///* ========================================================================
//   #MEDIA BLOCK
//   ======================================================================== */

.c-media-block {
    @include mq($from: desktop) {
        padding-left: 380px;
        position: relative;
        min-height: 320px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .c-section__title {
        @include mq($from: desktop) {
            text-align: left;
        }
    }

    &__img {
        width: 240px;
        height: 240px;
        margin: 34px auto 0;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
        @include mq($from: desktop) {
            width: 320px;
            height: 320px;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            margin-top: 0;
        }

        &--circle {
            border-radius: 50%;
            overflow: hidden;
        }

        img {
            width: 100%;
            height: auto;
        }
    }

    &__content:nth-child(3) {
        margin-top: 34px;
        @include mq($from: desktop) {
            margin-top: 24px;
        }
    }

    &__button {
        margin-top: 24px;
        text-align: center;
        @include mq($from: desktop) {
            text-align: left;
        }
    }

    .s-cms-content {
        @include mq($from: desktop) {
            text-align: left;
        }
    }
}