/*/ ========================================================================
//   #OFFICE LOCATOR PROFILE - HERO
//   ======================================================================== */
.c-office-hero {
    position: relative;
    @include mq($until: desktop) {
        display: flex;
        flex-direction: column-reverse;

    }
    @include mq($from: desktop) {
        margin-bottom: 80px;
    }

    &-banner {
        @include mq($from: desktop) {
            position: absolute;
            top: 0;
            width: 100%;
            z-index: 0;
        }

        &__img {
            height: 150px;
    
            @include mq($from: desktop) {
                height: 501px;
            }
        }
    }

    &__content {
        position: relative;
        z-index: 1;

        .o-wrapper {
            @include mq($until: tablet) {
                padding: 0;
            }
        }
        @include mq($from: desktop) {
            position: relative;
            top: 74px;

        }
    }

    &-nearest-store {
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
        background-color: $color-white;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        @include mq($from: desktop) {
            border-radius: 8px;
        }
    
        &__content {
            max-width: 460px;
            padding: 15px 20px 31px;

            @include mq($from: desktop) {
                padding: 25px 0px 31px 40px ;
            }
        }
    
        &__status {
            color: $color-blue;
            font-size: 20px;
            margin-bottom: 8px;
            strong {
                font-weight: 600;
            }
        }
        &__title {
            font-size: 20px;
            line-height: 1.2;
            @include mq($from: desktop) {
                font-size: 24px;
            }
        }
        &__info {
            margin-top: 13px;
            *{
                display: flex;
            }
            .o-icon {
                margin-right: 5px;
                transform: translate(0px, 2px);
            }
        }
        &__address {
            margin-bottom: 8px;
            @include mq($from: desktop) {
                margin-bottom: 20px;
            }
            .o-icon {
                color: $color-blue;
            }
        }
        &__hours,&__features {
            margin: 24px 0 8px;
            strong {
                font-weight: 600;
            }
        }
        &__map {
            width: 100%;
            height: 200px;
            @include mq($from: tablet) {
                height: 250px;
            }
            @include mq($from: desktop) {
                margin-top: 25px;
                margin-bottom: 25px;
                padding: 0 20px 0;
                width: 425px;
                height: 440px;
            }
            &-wrapper, #js-office-locator-profile-map {
                width: 100%;
                height: 100%;
            }
            
        }
        &__reference {
            font-size: 15px;
        }

        &__controls {
            display: flex;
        }
        &__control {
            margin: 18px 0 -9px;
            color: $color-black;
            &:first-child {
                margin-right: 16px;
                @include mq($from: tablet){
                    margin-right: 50px;
                }

            }
            a {
                color: inherit;
            }
            .o-icon {
                @include mq($from: tablet) {
                    margin-right: 5px;
                }
            }
            &:hover {
                color: $color-blue;
            }
            @include mq($until: tablet) {
                width: 45px;
                height: 45px;
                border-radius: 50%;
                border: 1px solid $color-blue-green;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 21px;
                &--text {
                    display: none;
                }
            }
        }
    }

}
/*/ ========================================================================
//   #OFFICE LOCATOR PROFILE - MESSAGE ALERT
//   ======================================================================== */
.c-office-hero-alert {
    padding: 30px 20px 0 20px;
    @include mq($from: desktop) {
        padding: 15px 20px 0 40px; 
    }
    &__content {
        width: 100%;
        border-radius: 8px;
        border: 1px solid $color-orange;
        display: flex;
        padding: 14px 15px 16px;
        position: relative;
        @include mq($from: desktop) {
            padding: 14px 35px 18px;
        }
    }
    &__img {
        margin-right: 25px;
        width: 100px;
        @include mq($until: desktop) {
            display: none;
        }
    }
    &__message { 
        strong {
            font-weight: 600;
            font-size: 18px;
            color: $color-orange;
        }
        span {
            color: $color-blue;
        }
    }
    &__close {
        background-color: $color-white;
        color: $color-orange;
        width: 24px;
        height: 24px;
        line-height: 0;
        font-size: 21px;
        position: absolute;
        right: -8px;
        top: -8px;
    }
}

/*/ ========================================================================
//   #OFFICE LOCATOR PROFILE - TOGGLE (office hours)
//   ======================================================================== */
.c-office-hero-toggle {

    strong {
        font-weight: normal;
        width: 130px;
    }
    
    &__item{
        display: flex;
        align-items: center;
        width: 100%;
    }

    &__content{
        position: relative;
        width: 100%;
        font-size: 15px;
        color: $color-blue-green;
        width: 275px;

        .c-office-hero-toggle__item p{
            margin: 0;
            width: 140px;
        }
        > button.c-office-hero-toggle__item  {
            background-color:$color-white;
            color: $color-blue-green;
            justify-content: space-between;
            
        }

        ul {
            display: none;
            width: 100%;
            color: $color-black;
        }
        
        .c-office-hero-toggle__icon {
            width: 14px;
            height: 14px;
            border: 1px solid $color-black;
            border-radius: 50%;
            right: 0;
            top: 4px;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: .3s ease;
            position: absolute;
            right: -29px;
            &:before , &:after{
                position: absolute;
                content: "";
                width: 8px;
                height: 1px;
                background-color: $color-black;
            }
            &:after {
                transform: rotate(90deg);
            }
            &:hover {
               
                &:before , &:after {
                    background-color: $color-blue-green;
                }         
            }
        }
        &.active {
            
            .c-office-hero-toggle__icon {
                border: 1px solid $color-blue-green;
                transform: rotate(90deg);
                &:after {
                    background-color: $color-blue-green;
                }
                &:before {
                    display: none;
                }
            }
        }
    }
    
} 

/*/ ========================================================================
//   #OFFICE LOCATOR PROFILE - Features
//   ======================================================================== */
.c-office-hero-features {

    &__item {
        .o-icon {
            color: $color-blue;
            margin-right: 8px;
        }
        a {
            color: $color-black;
            &:hover{
                color: $color-blue;
            }
        }
    }
}
