.s-cms-content {
    text-align: center;
    font-size: 16px;
    line-height: 1.5;

    + & {
        margin-top: 30px;
        @include mq($from: tablet) {
            margin-top: 35px;
        }
    }

    p {
        margin: 0 0 20px 0;
        
        &:last-child {
            margin-bottom: 0;
        }
    }

    ul + p, ol + p {
        margin-top: 24px;
        @include mq($from: tablet) {
            margin-top: 50px;
        }
    }

    a {
        text-decoration: underline;
        color: $color-blue;
        font-weight: 600;
    }

    strong {
        font-weight: 600;
        color: $color-blue;
    }

    ul, ol {
        padding: 0;
        margin: 24px auto -15px;
        max-width: 590px;
        text-align: left;
        @include mq($from: tablet) {
            margin-top: 50px;
            column-count: 2;
            column-gap: 50px;
        }
        @include mq($from: desktop) {
            column-gap: 65px;
        }
    }

    ul {
        list-style: disc;
        padding-left: 20px;

        li {
            padding-left: 5px;
            padding-bottom: 15px;
            -webkit-column-break-inside: avoid;
            page-break-inside: avoid;
            break-inside: avoid;
            @include mq($from: desktop) {
                padding-left: 10px;
            }

            &::marker {
                color: $color-green;
            }
        }
    }

    ol {
        padding-left: 18px;

        li {
            padding-bottom: 15px;
            -webkit-column-break-inside: avoid;
            page-break-inside: avoid;
            break-inside: avoid;
            @include mq($from: desktop) {
                padding-left: 12px;
            }
        }
    }
}

.wp-block-embed-youtube { text-align: center; }