///* ========================================================================
//   #MENU MOBILE NAV
//   ======================================================================== */

.c-menu-mobile-nav {

    &__item {
        border-bottom: 1px solid rgba(#2F80ED, 0.2);
    }

    &__link {
        padding: 20px 4px;
        display: flex;
        width: 100%;
        text-decoration: none;
        color: inherit;
        font-weight: 600;
        font-size: 18px;

        &.has-icon {
            padding-left: 36px;
            position: relative;
        }

        &.has-dropdown {
            padding-right: 36px;
            position: relative;
        }
    }

    &__item-icon {
        position: absolute;
        top: 50%;
        left: 4px;
        transform: translateY(-50%);
        color: $color-orange;
        font-size: 16px;
    }

    &__item-icon-dropdown {
        position: absolute;
        top: 50%;
        right: 4px;
        transform: translateY(-50%);
        color: $color-black;
        font-size: 18px;
        transition: $global-transition;

        .c-menu-mobile-nav__link.is-active & {
            transform: translateY(-50%) rotate(180deg);
            transition: $global-transition;
        }
    }

    &__sublist-wrapper {
        padding: 0 26px;
        display: none;
    }

    &__category-title {
        color: $color-blue;
        font-size: 16px;
        font-weight: 600;
        display: flex;
        align-items: center;
        position: relative;
        margin-bottom: 18px;
        padding: 4px 0 4px 50px;

        .o-icon {
            font-size: 36px;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }
    }

    &__sublist {
        padding-bottom: 28px;
    }

    &__subitem {
        margin-top: 18px;

        &:first-child {
            margin-top: 0;
        }
    }

    &__all-products {
        padding-bottom: 24px;
        margin-top: -6px;
    }

    &__sublink {
        display: block;
        text-decoration: none;
        color: inherit;
        font-size: 16px;

        .c-menu-mobile-nav__all-products & {
            text-decoration: underline;
            font-weight: 600;
        }
    }
}