.c-kc-faqs {
    &__container {
        @include mq($from: tablet) {
            display: flex;
        }
    }
    &__wrapper {
        @include mq($until: tablet) {
            text-align: center;
        }
    }
    .c-section__content {
        @include mq($until: tablet) {
            margin-top: 0;
        }
    }
    &-aside {
        width: 280px;
        width: 100%;
        display: none;
        @include mq($from: tablet) {
            display: block;
        }
        @include mq($from: desktop) {
            width: 340px;
        }
        &__title {

            font-family: $font-oswald-medium;
            font-size: 32px;
            font-weight: normal;
            padding-bottom: 35px;
            margin: 0 0 0;

        }
        &__content {
            overflow: auto;
            width: 280px !important;
            border-top: 1px solid $color-sky-blue;
            padding-top: 24px;
            @include mq($from: desktop) {
                width: 340px !important;
            }
            &::-webkit-scrollbar {
                -webkit-appearance: none;
                background-color: $color-light-blue;
            }
            &::-webkit-scrollbar:vertical {
                width: 5px;
            }
            &::-webkit-scrollbar-thumb {
                background-color: rgba(0,71,187,.5);
                border-radius: 20px;
            }
            
            &::-webkit-scrollbar-track {
                border-radius: 10px;  
            }
        }
        &-list {
            padding-top: 4px;
            height: calc(100% - 4px);

            &__item {
                color: $color-black;
                font-size: 18px;
                font-weight: 600;
                margin-bottom: 15px;
                padding-left: 4px;
                @include mq($from: desktop) {
                    margin-bottom: 30px;
                    font-size: 20px;
                }
                &:last-child {
                    margin-bottom: 0;
                }

                &:first-child {
                    padding-top: 4px;
                }

                &.is-active,&:hover {
                    color: $color-blue;
                    .c-kc-faqs-aside-list__link {
                        text-decoration: underline;
                    }
                }
            }
            &__link {
                color: inherit;
                text-decoration: none;
            }
        }

        &__controls {
            color: $color-orange;
            font-weight: 600;
            border-top: 1px solid $color-sky-blue;
            margin-top: 35px;
            padding-top: 35px;
            font-size: 20px;
            a {
                color: inherit;
            }
        }
    }

    &-aside__select {
        max-width: 450px;
        width: 100%;
        height: 62px;
        padding: 5px 26px 5px 18px;
        font-weight: 600;
        margin: 0 auto;
        text-align: left;
        margin-bottom: 45px;
        @include mq($from: tablet) {
            display: none;
        }
    }
}
.c-kc-faqs-content {
    @include mq($from: tablet) {
        padding-left: 50px;
    }
    @include mq($from: desktop) {
        padding-left: 65px;
    }

    &__heading {
        display: flex;
        align-items: center;
    }
    &__icon {
        width: 40px;
        height: 40px;
        @include mq($from: tablet) {
            width: 60px;
            height: 60px;
        }
    }   
    &__title {
        color: $color-blue;
        font-family: $font-oswald;
        font-size: 20px;
        margin: 0 0 0 14px;
        @include mq($from: tablet) {
            font-size: 32px;
            margin: 0 0 0 20px;
        }
    }     
}
.c-kc-faqs-questions{
    &__title {
        color: $color-blue;
        font-weight: 600;
        font-size: 18px;
        line-height: 1.2;
        margin-top: 30px;
        &:first-child {
            margin-top: 20px;
        }
        @include mq($from: tablet) {
            font-size: 22px;
        }
    }
    
    &__text {
        a {
            color: $color-blue;
        }
        strong {
            font-weight: 600;
        }
    }
    &__subitem {
        padding-left: 20px;
        @include mq($from: tablet) {
            padding-left: 40px;
        }
    }
}