.kc-teaser-home {

    @include mq($from: desktop) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__item {
        margin-bottom: 30px;
        width: 100%;
        @include mq($from: desktop) {
            margin-bottom: 20px;

            &:nth-last-child(2) {
                margin-bottom: 0;
            }
        }

        &:last-child {
            margin-bottom: 0;
        }

        &--50 {
            @include mq($from: desktop) {
                width: calc(50% - 10px);
            }
        }
    }

    &__controls {
        margin-top: 20px;
    }

    &-preview {
        background-color: #fff;
        box-shadow: 0px 2px 4px rgba(65, 70, 72, 0.15);
        display: flex;

        &--big {
            display: block;
            @include mq($from: desktop) {
                display: flex;
            }
        }

        &__img-link {
            display: block;
            width: 50%;

            .kc-teaser-home-preview--big & {
                width: 100%;
                @include mq($from: desktop) {
                    width: 50%;
                }
            }
        }

        &__img {
            min-height: 210px;
            height: 100%;

            .kc-teaser-home-preview--big & {
                min-height: 186px;
                @include mq($from: tablet) {
                    min-height: 300px;
                }
            }
        }

        &__content {
            width: 50%;
            padding: 15px 20px;

            .kc-teaser-home-preview--big & {
                width: 100%;
                @include mq($from: desktop) {
                    width: 50%;
                    padding: 25px 45px;
                }
            }
        }

        &__date {
            font-size: 14px;
        }
        &__categorie {
            color: $color-black;
            text-decoration: none;
            font-weight: 600;
            &:hover {
                color: $color-blue;
                text-decoration: underline;
            }
        }

        &__title {
            margin: 10px 0 0 0;
            font-weight: 600;
            font-size: 20px;
            line-height: 1.2;
            @include mq($from: desktop) {
                font-size: 22px;
            }

            .kc-teaser-home-preview--big & {
                font-size: 24px;
                @include mq($from: desktop) {
                    font-size: 28px;
                }
            }

            a {
                color: $color-black;
                text-decoration: none;

                &:hover {
                    text-decoration: underline;
                    color: $color-blue;
                }
            }
        }

        &__text {
            margin: 6px 0 0 0;
            font-size: 16px;
        }

        &__link {
            font-weight: 700;
            font-size: 16px;
            text-decoration: none;
            color: $color-black;
            display: inline-block;
            margin-top: 12px;
            @include mq($from: desktop) {
                font-size: 18px;
            }

            &:hover {
                text-decoration: underline;
                color: $color-blue;
            }
        }
    }
}