.u-text {
    &-small {
        font-size: 14px;
    }
    &-centered {
        text-align: center;
    }
    &--left {
        text-align: left;
    }
    &-italic{
        font-style: italic;
    }
    &-underline {
        text-decoration: underline;
    }
    &-uppercase {
        text-transform: uppercase;
    }

}