.c-hero-home {
    background-color: $color-light-blue;
    position: relative;
    &__content-image {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 620px;
        display: block;

        @include mq($from: tablet) {
            height: 600px;
        }
    }
    &__image {
        img {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: bottom;
            @media screen and (min-width: 0\0 ){
                @include mq($from: desktop) {
                    bottom: -16%;
                }
                @include mq($from: 1500px) {
                    bottom: -48px;
                }
            }
        }
    }
    &__container {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        margin: 0 auto;
        padding-top: 45px;
        width: 100%;
        @include mq($from: wide) {
            padding-top: 80px;
        }
    }
    &__title {
        font-family: $font-oswald-medium;
        text-align: center;
        margin: 0;
        line-height: 1.06;
        font-size: 32px;

        @include mq($from: desktop) {
            font-size: 52px;
        }
        span {
            color: $color-blue;
        }
        strong {
            font-weight: normal;
        }
    }
    &__subtitle {
        font-family: $font-oswald;
        font-weight: 400;
        font-size: 28px;
        margin-top: 0;
        margin-bottom: 25px;
        text-align: center;
        line-height: 1;
        margin-top: 10px;
        @include mq($from: mobile) {
            font-size: 24px;
        }
        @include mq($from: desktop) {
            font-size: 40px;
            margin-bottom: 27px;
        }

        a {
            font-family: Arial, Helvetica, sans-serif;
            font-size: 50%;
            font-weight: 600;
            color: $color-black;
            text-decoration: none;
            vertical-align: top;
            display: inline-block;
            transform: translate(4px, 4px);

            &:hover {
                text-decoration: underline;
                color: $color-blue;
            }
        }
    }
}