.c-office-locator-results {
    width: calc(100% + 40px);
    margin-left: -20px;
    margin-top: 30px;
    @include mq($from: wide) {
        width: calc(100% + 20px);
    }

    &__item {
        border-bottom: 1px solid #DFDFDF;

        &:last-child {
            border-bottom: 0;
        }
    }
    
    .s-cms-content {
        text-align: left;
        padding: 0 20px 30px;

        ul {
            margin-left: 0;
            column-count: 1;
        }

        button {
            text-decoration: underline;
            font-weight: 600;
            background-color: transparent;

            &:hover {
                color: $color-blue;
            }
        }
    }
}


.c-office-teaser {
    background-color: #fff;
    padding: 25px 20px 30px;
    @include mq($from: desktop) {
        padding: 20px 200px 20px 30px;
        position: relative;
    }

    &:hover {
        background-color: $color-light-blue;
    }

    &__link {
        text-decoration: none;
        color: $color-black;
    }

    &__title {
        font-size: 16px;
        font-weight: 600;
        line-height: 1.37;
        margin: 0;
        display: inline;
    }

    &__distance {
        font-weight: normal;
    }

    &__rating {
        margin-top: 10px;
    }

    &__phone {
        display: none;
        margin-top: 10px;
        text-decoration: underline;
        color: $color-black;
        font-size: 16px;

        @include mq($from: desktop) {
            display: inline-flex;
            align-items: center;
        }

        &:hover {
            color: $color-blue;
        }

        .o-icon {
            font-size: 16px;
            margin-right: 10px;
            text-decoration: none;
        }
    }

    &__time {
        margin-top: 10px;
    }

    &__status {
        font-weight: 600;
        font-size: 16px;

        &--open {
            color: $color-blue;
        }

        &--closed {
            color: $color-orange;
        }
    }

    &__controls {
        margin-top: 20px;
        display: flex;
        @include mq($from: desktop) {
            position: absolute;
            top: 20px;
            right: 30px;
            margin-top: 0;
        }
    }

    &__control {
        margin-right: 16px;
        display: block;
        text-align: center;
        text-decoration: none;

        &--phone {
            @include mq($from: desktop) {
                display: none;
            }
        }

        .o-icon {
            line-height: 0.6;
            font-size: 18px;
            width: 36px;
            height: 36px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            border: 1px solid $color-blue-green;
            color: $color-blue-green;
            transition: color 0.2s ease-in-out;
            margin-left: auto;
            margin-right: auto;
            @include mq($from: desktop) {
                color: $color-black;
            }
        }

        &:hover .o-icon {
            color: $color-blue-green;
            transition: color 0.2s ease-in-out;
        }

        &:last-child {
            margin-right: 0;
        }

        &-text {
            font-size: 12px;
            text-decoration: none;
            color: $color-black;
            margin-top: 6px;
            @include mq($from: desktop) {
                display: block;
            }
        }
    }

    &__affiliated {
        margin-top: 22px;
        display: flex;
        align-items: center;
        @include mq($from: desktop) {
            margin-top: 10px;
        }

        &-text {
            font-weight: 600;
            font-size: 14px;
            color: $color-blue-green;
        }

        &-logo {
            display: block;
            margin-left: 10px;
        }
    }
}

.c-office-teaser-rating {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: 15px;
    @include mq($from: desktop) {
        font-size: 16px;
    }
    
    &__stars {
        margin-left: 8px;
        display: flex;
        align-items: center;
    }

    &__star {
        display: block;
        margin-left: 7px;

        &:first-child {
            margin-left: 0;
        }
    }

    &__reviews {
        margin-left: 9px;
        text-decoration: none;
        color: $color-black;

        &:hover {
            color: $color-blue;
        }
    }
}