.c-steps-list {

    .s-cms-content + & {
        margin-top: 30px;        

        @include mq($from: desktop) {
            margin-top: 48px;
        }
    }

    &__list {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }

    &__item {
        width: 100%;
        margin-bottom: 35px;
        &:last-child{
            margin: 0;
        }

        @include mq($from: tablet) {
            width: calc(33.33% - 30px);
            margin-bottom: 0;
            margin: 0 15px 0 0;
        }

        @include mq($from: desktop) {
            width: calc(33.33% - 60px);
            margin: 0 90px 35px 0;
        }
    }

    &__img {
        width: 70px;
        height: auto;
        display: block;
        @include mq($from: desktop) {
            width: 80px;
        }
    }

    &__content {
        display: flex;
        flex-wrap: wrap;
        margin-top: 29px;
    }

    &__step {
        width: 42px;
        height: 42px;
        border-radius: 50%;
        background-color: $color-sky-blue;
        color: $color-blue;
        font-size: 36px;
        font-weight: 600;
        display: flex;
        line-height: 1;
        justify-content: center;
        align-items: center;

        @include mq($from: desktop) {
            width: 52px;
            height: 52px;
            font-size: 42px;
        }
    }

    &__text {
        width: calc(100% - 60px);
        padding-left: 8px;

        p {
            margin-top: 0;
        }
        a {
            color: $color-blue;
        }
        strong {
            color: $color-blue;
            font-weight: 600;
        }
    }
}