.c-section {
    padding: 40px 0;
    background-color: #fff;

    &--light-blue {
        background-color: $color-light-blue;
    }

    &--small-spacing {
        padding: 36px 0;
    }
    &--spacing-bottom {
        padding-bottom: 26px ;
    }
    &--top-0-spacing {
        padding-top: 0;
    }
    &--bottom-0-spacing {
        padding-bottom: 0;
    }
    &--footer-disclaimer {
        margin-top: -50px;
        position: relative;
        @include mq($from: desktop) {
            margin-top: -50px;
        }
    }

    &__title {
        font-family: $font-oswald;
        text-align: center;
        font-size: 30px;
        color: $color-blue;
        font-weight: normal;
        margin: 0;
        line-height: 1.2;

        strong {
            font-family: $font-oswald-medium;
            font-weight: normal;
        }

        @include mq($from: desktop) {
            font-size: 36px;
        }

        &--black {
            color: $color-black;
        }

        &--small {
            font-size: 24px;
            @include mq($from: desktop) {
                font-size: 24px;
            }
        }
        &--medium{
            font-size: 28px;
            @include mq($from: desktop) {
                font-size: 24px;
            }
        }
        &--big {
            font-size: 28px;
            @include mq($from: desktop) {
                font-size: 52px;
            }
        }
        &--blue-strong {
            color: $color-blue;
            font-weight: 600;
        }
        + .c-section__title--black  {
            font-size: 24px;
            margin-top: 24px;
            @include mq($from: desktop) {
                font-size: 28px;
            }
        }
    }

    &__intro-text {
        text-align: center;
        font-size: 16px;
        margin-top: 10px;
        @include mq($from: wide) {
            margin-top: 15px;
        }

        p {
            margin: 0 10px 0;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__content {
        margin-top: 56px;

        &:only-child {
            margin: 0;
        }

        .c-section__intro-text + &, &--small-top-margin {
            margin-top: 30px;
            @include mq($from: wide) {
                margin-top: 35px;
            }
        }
        /* looks like this cause a bug in the kc section home
        .c-section__intro-text + &, &--0-top-margin {
            margin-top: 0;
        }*/
    }
}
