.c-contact-us {
    &__wrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &__item {
        max-width: 370px;
        width: 100%;
        border: 1px solid $color-silver;
        padding: 25px 15px 25px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;

        &:first-child {
            @include mq($from: tablet) {
                margin-right: 10px;
            }
            
        }
    }
    &__title {
        color: $color-blue;
        font-family: $font-oswald;
        font-size: 24px;
        text-align: center;
        @include mq($from: tablet) {
            font-size: 28px;
        }
    }
    &__controls {
        max-width: 260px;
        width: 100%;
        margin: auto;
        &-item {
            color: $color-black;
            display: block;
            text-decoration: none;
            margin-top: 15px;
        }
        &-icon {
            color: $color-blue;
            margin-right: 11px;
        }
        
    }

    &-info {
        max-width: 391px;
        width: 100%;
        margin-bottom: 50px;
        &__item {
            margin-top: 30px;
            @include mq($from: desktop) {
                margin-top: 36px;
            }
        }

        &__title {
            color: $color-blue;
            font-size: 28px;
            font-family: $font-oswald;
            font-weight: 400;
            margin: 0;
        }
        &__description {
            margin-top: 8px;
        }
    }
}