.c-office-stores{
    &__content {
        display: flex;
        flex-wrap: wrap;
        margin-top: 24px;
        @include mq($from: tablet) {
            margin-top: 35px;
        }
    }

    &__item {
        width: 100%;
        background-color: $color-light-blue;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
        margin-bottom: 30px;
        padding: 27px 21px 23px;

        @include mq($from: tablet){
            margin: 0 40px 45px 0;
        }
        @include mq($from: desktop) {
            margin: 0 20px 45px 0;
            background-color: $color-white;
            
            &:nth-child(3n) {
                margin: 0 0 45px 0;
            }
        }
        @include mq($from: wide){
            margin: 0 30px 45px 0;
        }

        &--50 {
            @include mq($from: tablet) {
                width: calc(50% - 51px);
            }
        }

        &--33 {
            @include mq($from: desktop) {
                max-width: 300px;
            }

        }
    }

    &__address {
        margin: 0;
        color: $color-black;
        text-decoration: none;

        strong {
            font-weight: 600;
        }
        &:hover {
            color: $color-blue;
        }
    }

    &__status {
        margin-top: 8px;

        strong {
            font-weight: 600;
        }

        &--open {
            strong {
                color: $color-blue;
            }
        }
        &--closing {
            strong {
                color: $color-blue;
            }
        }
        &--closed {
            strong {
                color: $color-orange;
            }
        }
    }
    
    &__phone {
        margin-top: 9px;
        @include mq($until: desktop){
            display: none;
        }
        &-number {
            color: $color-black;

            .o-icon {
                color: $color-orange;
                margin-right: 7px;
                font-size: 17px;
            }
        }
        
    }

    .c-office-teaser__controls {
        position: relative;
        top: 0;
        right: 0;
    }
}