.c-hero-home-radio {
    &__wrapper {
        max-width: 580px;
        width: 100%;
        margin: auto;
        border: 2px solid $color-silver;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
        background-color: $color-white;
        border-radius: 8px;
        padding: 5px 8px 10px;

        @include mq($from: 580px) {
            padding: 16px 40px 10px;
        }
        @include mq($from: desktop) {
            padding: 10px 36px 10px;
        }
    }
    &__form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;

        @include mq($from: desktop) {
            margin-bottom: 29px;
        }

        &-title {
            font-size: 16px;
            font-weight: 600;
            margin-bottom: 21px;
            padding-top: 13.68px;
            text-align: center;
            width: 100%;
        }
        &-content {
            border: 0;
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            padding: 0;
            width: 100%;
        }

        &-item {
            width: 120px;
            display: flex;
            justify-content: center;
            text-align: center;
            height: 62px;
            position: relative;
            margin: 0 5px 12px;

            html[lang=es-US] & {
                @include mq($until: 450px) {
                    width: 138px;
                }
            }

            @include mq($from: 450px) {
                max-width: 180px;
                width: 100%;
            }

            @include mq($from: tablet) {
                max-width: 237px;
            }

            @include mq($from: desktop) {
                height: 68px;
                max-width: 242px;
            }
        }
        fieldset {
            border: 0;
            padding: 0;
            width: 100%;
            margin: 0;
        }

        .form.c-hero-home-radio__form-label{
            width: 100%;
            height: 100%;
            position: absolute;
            text-align: center;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            border-radius: 10px;
            left: 0;
            padding-left: 14.5px;

            @include mq($until: tablet) {
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding-left: 0;
            }
        }        

        .c-hero-home-radio__form-text{
            font-size: 15px;
            color:$color-black;
            font-weight: 700;
            display: flex;
            align-items: center;
            
            @include mq($from: tablet) {
                margin-left: 10px;
            }

            @include mq($from: desktop) {
                font-size: 20px;
                margin-left: 10px;
                html[lang=es-US] & {
                    font-size: 18px;
                }
            }
        }
        input[type="radio"] {
            opacity: 0;
            position: absolute;
            
            + label {
                border: 2px solid $color-silver;
            }
            
            &:checked {
                + label {
                    border: 2px solid $color-orange;
                    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
                    .c-hero-home-radio__form-icon-check {
                        display: block;
                        color: $color-green;
                    }
                    .c-hero-home-radio__form-text {
                        color: $color-orange;
                    }
                }
            }
            &:focus {
                + label {
                    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
                }
              }
        }
        &-icon-check {
            display: none;
            padding: 0 0 0px 5px ;
        }
        &-icon {
            font-size: 33px;
            color: $color-blue;
            height: 25px;
            margin-bottom: 5px;

            @include mq($from: desktop) {
                font-size: 56px;
                height: 43px;
            }
        }
        &-zipcode {
            width: 100%;
            margin: 0 auto 12px;
            color: $color-black;
            text-align: center;

            @include mq($from: tablet) {
                font-size: 24px;
                width: 40.5%;
                margin: 0 20px 0 0;
                text-align: left;
                html[lang=es-US] & {
                    width: 34.615%;
                }
            }
            
            label.form__error {
                background-color: $color-orange;
                color: #fff;
                font-size: 12px;
                padding: 6px 9px;
                display: inline-block;
                margin-top: 7px;
                border-radius: 6px;
                position: relative;
                margin-left: 10px;
                box-shadow: 0 3px 6px 0 rgba(0,0,0,.16);
                @include mq($from: tablet) {
                    font-size: 15px
                }
                &::before {
                    content: '';
                    display: inline-block;
                    position: absolute;
                    top: -5px;
                    left: 20px;
                    width: 1px;
                    border-left: 4px solid transparent;
                    border-right: 4px solid transparent;
                    border-bottom: 5px solid $color-orange;
                }
            }
        }
        &-items {
            width: 170px;
            margin: 2px auto 0;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            @include mq($from: 575px) {
                width: 200px;
            }
            @include mq($from: tablet) {
                flex-direction: row;
                margin: 6px 0 0;
                width: 100%;
                justify-content: center;
            }
           
            &-input {
                width: 100%;
                height: 50px;
                border-radius: 8px;
                box-shadow: 0px 0px 6px rgba(0, 71, 187, 0.8);
                border: 0;
                padding: 5px 12px;
                @include placeholder(#959595);
                font-size: 20px;
                font-weight: 600;
                &.form__valid {
                    border: 1px solid $color-green;
                }
        
                &.form__error {
                    border: 1px solid $color-orange;
                }
            
            }
            .c-button {
                font-size: 18px;
                padding: 5px 33px;
                @include mq($from: tablet) {
                    font-size: 24px;
                }
            }
        }
    }
    &__link {
        display: block;
        text-align: center;
        text-decoration: none;
        color: $color-black;
        font-size: 14px;
        padding-bottom: 12px;
        transition: color 0.2s ease-in-out;
        @include mq($from: tablet) {
            font-size: 18px;
            padding-bottom: 15px;
        }

        &-button {
            background-color: $color-white;
            font-weight: 600;
            color: $color-black;
        }

        &:hover &-button {
           color: $color-blue;
           transition: color 0.2s ease-in-out;
        }
    }
}
