///* ========================================================================
//   #SITE HEADER TEASER BAR
//   ======================================================================== */

.teaser-bar {
    background-color: #fff;
    padding: 13px 50px 13px 55px;
    display: flex;
    align-items: center;
    position: relative;

    &__icon {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        color: $color-blue;
        font-size: 30px;
        left: 20px;
    }

    &__link {
        display: block;
        line-height: 1;
        color: inherit;
        text-decoration: none;
        font-size: 16px;
        color: $color-blue;
        @include mq($from: desktop) {
            font-size: 18px;
        }

        strong {            
            font-family: $font-oswald-medium;
            font-weight: normal;
        }

        span {
            color: $color-orange;
            font-family: $font-oswald;
            text-decoration: underline;
        }
    }

    &__close {
        background-color: transparent;
        position: absolute;
        top: 54%;
        right: 20px;
        transform: translateY(-50%);
        display: block;
        line-height: 0.8;

        .o-icon {
            font-size: 18px;
        }
    }
}